.vux-header {
  position: relative;
  padding: 3px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fff;
}
.vux-header .vux-header-title {
  line-height: 40px;
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  color: #333;
}
.vux-header-title-area,
.vux-header .vux-header-title {
  margin: 0 88px;
  height: 40px;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.vux-header .vux-header-title > span {
  display: inline-block;
}
.vux-header .vux-header-left,
.vux-header .vux-header-right {
  position: absolute;
  top: 14px;
  display: block;
  font-size: 14px;
  line-height: 21px;
  color: #999;
}
.vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {
  float: left;
  margin-right: 8px;
  color: #999;
}
.vux-header .vux-header-left a:active,
.vux-header .vux-header-left button:active,
.vux-header .vux-header-right a:active,
.vux-header .vux-header-right button:active {
  opacity: 0.5;
}
.vux-header .vux-header-left {
  left: 18px;
}
.vux-header .vux-header-left .vux-header-back {
  padding-left: 16px;
}
.vux-header .vux-header-left .left-arrow {
  position: absolute;
  width: 30px;
  height: 30px;
  top: -5px;
  left: -5px;
}
.vux-header .vux-header-left .left-arrow:before {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  border: 1px solid #333;
  border-width: 1px 0 0 1px;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
  top: 8px;
  left: 7px;
}
.vux-header .vux-header-right {
  right: 15px;
}
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {
  margin-left: 8px;
  margin-right: 0;
}
.vux-header .vux-header-right .vux-header-more:after {
  content: "\2022   \2022   \2022   ";
  font-size: 16px;
}
.vux-header-fade-in-right-enter-active {
  -webkit-animation: fadeinR 0.5s;
          animation: fadeinR 0.5s;
}
.vux-header-fade-in-left-enter-active {
  -webkit-animation: fadeinL 0.5s;
          animation: fadeinL 0.5s;
}
@-webkit-keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes fadeinL {
0% {
    opacity: 0;
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinL {
0% {
    opacity: 0;
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
